import React from "react";
import {reduxForm, Field, InjectedFormProps} from "redux-form";
import {Input} from "../../../common/form-controls/Input";
import {requiredField} from "../../../utils/validators/validators";
import s from "../../../common/form-controls/errorField.module.css";

//     form   input/Field
export type LoginReduxFormType = {
    login: string;
    password: string;
    rememberMe: boolean;
};

const LoginRedux = (props: InjectedFormProps<LoginReduxFormType>) => {
    return (
        // onSubmit  props.handleSubmit    
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field
                    placeholder={"login"} //     "input"
                    name={"login"}
                    component={Input}
                    validate={[requiredField]} //-  validate   redux-form
                />
            </div>
            <div>
                <Field placeholder={"password"} name={"password"} component={Input} validate={[requiredField]}/>
            </div>
            <div>
                <Field type={"checkbox"} name={"rememberMe"} component={Input}/> remember me
            </div>
            <div className={s.errorMessage}>
                {/*    thunk  action stopSubmit  redux-form*/}
                {props.error}
            </div>
            <button>submit</button>
        </form>
    );
};

//   reduxForm     'login'
export const LoginReduxForm = reduxForm<LoginReduxFormType>({form: "login"})(LoginRedux);
